.projects-logo-container {
	display: flex;
	justify-content: left;
	padding-top: 25px;
}

.is-down-text {
	font-size: 16px;
	color: red;
	margin-bottom: 10px;
	text-align: center;
	display: flex;
	justify-content: center;
}

.projects-logo {
	display: flex;
	position: fixed;
	border: 1px solid white;
	border-radius: 50%;
	box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
	top: 4vh;
	z-index: 999;
}

.projects-container {
	display: flex;
	flex-direction: column;
	height: 100%;
	padding: 50px;
	margin: 0;
	/* padding-top: 120px; */
}

.project-image {
	width: 100%;
	height: 500px;
	object-fit: cover;
}

.image-list {
	margin-top: 50px;
	margin-bottom: 30px;
}

@media (max-width: 600px) {
	.projects-container {
		/* padding-top: 60px; */
	}

	.projects-project {
		width: calc(100% / 2);
	}
}
